body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: -webkit-linear-gradient(right, rgba(7,27,8,10) 0%, rgba(0,128,128,10) 100%);
  background: linear-gradient(to left, rgba(7,27,8,10) 0%, rgba(0,128,128,10) 100%); /* w3c */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
font-family: 'SEGA LOGO FONT';
src: url(/Robofriends/static/media/SEGA.82e09f78.TTF) format('truetype');
}
@font-face {
font-family: 'ROBOT FONT';
src: url(/Robofriends/static/media/ROBOTS.6b84deb7.ttf) format('truetype');
}

.heading {
  font-family: 'SEGA LOGO FONT';
  font-weight: 300;
  color: orange;
}

h2{
  font-family: 'ROBOT FONT';
  font-weight: 300;
  color: beige;
}


